import React, { useState } from 'react';
import { Routes, Route } from 'react-router-dom';

import Home from './Home';
import CreatingAccount from './CreatingAccount';
import OneTimePasscode from './OneTimePasscode';

const SUB_DOMAIN = window.location.hostname === 'developers.liveperson.com' ? 'self-service' : 'free-trial';
const API_URL = `https://${SUB_DOMAIN}.liveperson.com/api/free-trial`;

function App() {
  const [messageState, setMessageState] = useState('creating');
  const [accountDetails, setAccountDetails] = useState({});
  const [userDetails, setUserDetails] = useState(
    {
      firstName: '',
      lastName: '',
      company: '',
      email: '',
      password: '',
      region: null,
      recaptchaResponseToken: '',
      role: null,
      intent: null,
      otherIntent: '',
    },
  );

  const resetRegistration = () => {
    setMessageState('creating');
    setAccountDetails(null);
    setUserDetails({ ...userDetails, password: '' }); // Reset password
  };

  return (
    <Routes>
      <Route
        exact
        path={`${process.env.PUBLIC_URL}/enter-passcode`}
        element={(
          <OneTimePasscode
            setMessageState={setMessageState}
            userDetails={userDetails}
            setAccountDetails={setAccountDetails}
            apiUrl={API_URL}
          />
        )}
      />
      <Route
        path={`${process.env.PUBLIC_URL}/creating-account`}
        element={(
          <CreatingAccount
            userDetails={userDetails}
            resetRegistration={resetRegistration}
            messageState={messageState}
            setMessageState={setMessageState}
            accountDetails={accountDetails}
            setAccountDetails={setAccountDetails}
            apiUrl={API_URL}
          />
        )}
      />
      <Route
        path={`${process.env.PUBLIC_URL}/`}
        element={(
          <Home
            setMessageState={setMessageState}
            userDetails={userDetails}
            setUserDetails={setUserDetails}
            apiUrl={API_URL}
          />
        )}
      />
    </Routes>
  );
}

export default App;
