import React, { useState, useEffect, useRef } from 'react';
import { Box } from 'theme-ui';
import Select, { components } from 'react-select';
import ErrorText from './ErrorText';
import theme from '../theme';
import DropdownIndicator from './DropdownIndicator';
import Option from './Option';
import Input from './Input';

function ValueContainer(props) {
  return (
    <Box
      data-testid="value-container"
      sx={{
        color: props.selectProps?.value
          ? theme.colors.navy
          : theme.colors.darkGray,
        flex: 1,
      }}
    >
      <components.ValueContainer {...props} />
    </Box>
  );
}

/**
 * Used for selecting a single option from a defined set
 *
 * @visibleName Select Field
 */
function SelectField({
  name,
  placeholder,
  value,
  handleChange,
  error,
  handleBlur,
  options,
  height,
  forceFocus,
}) {
  const [menuIsOpen, setMenuIsOpen] = useState(false);
  const isErrored = error && error.isError;
  const node = useRef();

  const detectOutsideClick = (e) => {
    if (!node.current.contains(e.target)) {
      setMenuIsOpen(false);
    }
  };

  // Detect outside clicks
  useEffect(() => {
    document.addEventListener('mousedown', detectOutsideClick);
    return () => {
      document.removeEventListener('mousedown', detectOutsideClick);
    };
  }, []);

  const getStyles = () => {
    const basicStyle = {
      boxSizing: 'border-box',
      display: 'flex',
      alignItems: 'center',
      flexWrap: 'nowrap',
      justifyContent: 'space-between',
      position: 'relative',
      borderRadius: 8,
      borderStyle: 'solid',
      borderWidth: 1,
      fontFamily: '"Roboto", sans-serif',
      fontSize: theme.fontSizes[1],
      height: theme.rowHeight,
      letterSpacing: '0.01em',
      lineHeight: '38px',
      WebkitFontSmoothing: 'antialiased',
      outlineStyle: 'auto',
      outlineColor: theme.colors.navyGray,
      '&:hover': {
        outlineWidth: 2,
      },
      '::-ms-expand': {
        display: 'none',
      },
      color: theme.colors.navyGray,
      gridArea: 'input',
      msGridColumn: '1',
      '&::placeholder': {
        color: 'darkGray',
      },
    };

    const baseStyles = {
      valueContainer: (provided) => ({
        ...provided,
        height: '100%',
        padding: '0 0 0 0',
        margin: '0 0 0 16px',
        position: 'absolute',
        top: 0,
        right: 0,
        left: 0,
        bottom: 0,
      }),
      input: (provided) => ({
        ...provided,
        width: 'auto',
        margin: 0,
        paddingBottom: 0,
        paddingTop: 0,
      }),
      indicatorSeparator: (provided) => ({
        ...provided,
        display: 'none',
      }),
      menu: (provided) => ({
        ...provided,
        backgroundColor: 'transparent',
        margin: 0,
        padding: 0,
        borderRadius: theme.space[2],
        overflow: 'hidden',
        color: 'red',
      }),
      menuList: (provided) => ({
        ...provided,
        margin: 0,
        padding: 0,
        borderRadius: theme.space[2],
        boxShadow: '0px 0px 16px rgba(132, 121, 112, 0.64)',
        maxHeight: height && `${height}px`,
        overflow: 'auto',
      }),
      placeholder: (provided) => ({
        ...provided,
        fontFamily: '"Roboto", sans-serif',
        fontSize: theme.fontSizes[1],
        color: theme.colors.darkGray,
        whiteSpace: 'nowrap',
        textOverflow: 'ellipsis',
        overflow: 'hidden',
        position: 'absolute',
        top: 0,
        bottom: 0,
      }),
      singleValue: (provided) => ({
        ...provided,
        position: 'absolute',
        top: 0,
        bottom: 0,
        right: '30px',
        left: 0,
      }),
    };

    if (isErrored) {
      return {
        ...baseStyles,
        control: (provided, state) => ({
          ...basicStyle,
          outlineWidth: state.isFocused ? 2 : 0,
          borderColor: 'red',
          backgroundColor: 'lightRed',
          // HACK: colorize webkit autocomplete input fields
          boxShadow: 'inset 0 0 0 1px rgba(255, 255, 255, 0), inset 0 0 0 100px #FEECEC',
        }),
      };
    }
    return {
      ...baseStyles,
      control: (provided, state) => ({
        ...basicStyle,
        outlineWidth: state.isFocused || forceFocus ? 2 : 0,
        borderColor: 'gray',
        // HACK: colorize webkit autocomplete input fields
        boxShadow:
          'inset 0 0 0 1px rgba(255, 255, 255, 0), inset 0 0 0 100px white',
      }),
    };
  };

  return (
    <Box>
      <Box
        sx={{
          cursor: 'pointer',
          position: 'relative',
          display: ['block', 'grid'],
          gridTemplateColumns: ['auto', '5fr 4fr'],
          gridTemplateAreas: "'input spacer'",
          '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
            display: '-ms-grid',
            msGridColumns: '5fr 4fr',
          },
        }}
        onClick={() => setMenuIsOpen((s) => !s)}
        ref={node}
      >
        <Select
          name={`select-field-${name}`}
          options={options}
          value={value}
          styles={getStyles()}
          onChange={(val) => {
            handleChange(name, val);
          }}
          onBlur={() => {
            handleBlur(name, { hasInteracted: true });
          }}
          placeholder={placeholder}
          components={{
            Input, ValueContainer, DropdownIndicator, Option,
          }}
          menuIsOpen={menuIsOpen}
        />
        <Box sx={{ gridArea: 'spacer', msGridColumn: '2' }} />
      </Box>
      <ErrorText error={error} mb={3} />
    </Box>
  );
}

SelectField.defaultProps = {
  name: 'select-field',
  value: undefined,
  options: [],
  error: {},
  forceFocus: false,
  placeholder: 'Select...',
  height: 224,
};

export default SelectField;
