import React, { useState, useEffect } from 'react';
import {
  Flex, Box, Image, Text,
} from 'theme-ui';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import PropTypes from 'prop-types';
import axios from 'axios';
import Logo from './Logo';
import ResendStatus from './ResendStatus';
import CompactSelectField from './CompactSelectField';
import InputField from './InputField';
import ShieldIcon from '../assets/icon-shield.svg';
import ErrorIcon from '../assets/icon-error.svg';
import theme from '../theme';
import Button from './Button';

import languages from '../assets/languages.json';

const LOGO_SIZE = 44;
const INPUT_SIZE = 44;
const INPUTS = [1, 2, 3, 4, 5];
const SERVER_ERROR = 'SERVER_ERROR';
const OTP_EXCEEDED = 'OTP_CREATION_LIMIT_EXCEEDED';
const OTP_VERIFICATION_LIMIT_EXCEEDED = 'OTP_VERIFICATION_LIMIT_EXCEEDED';
const OTP_CREATION_LIMIT_EXCEEDED = 'OTP_CREATION_LIMIT_EXCEEDED';

const getLanguage = (lang) => languages.find((l) => l.value === lang);

// TOOD: clean up these styles and put them in the theme file where possible
// TODO: refactor this file into smaller components

const gridTemplateColumns = `${INPUT_SIZE}px ${INPUT_SIZE}px ${INPUT_SIZE}px ${INPUT_SIZE}px ${INPUT_SIZE}px`;

let interval;

function OneTimePasscode({ apiUrl }) {
  const [digits, setDigits] = useState(['', '', '', '', '']);
  const [errors, setErrors] = useState([false, false, false, false, false]);
  const [errorMessage, setErrorMessage] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [resendSeconds, setResendSeconds] = useState(null);
  const [selectedDigit, setSelectedDigit] = useState(0);
  const [resendAttempts, setResendAttempts] = useState(0);
  const location = useLocation();
  const navigate = useNavigate();
  const creationId = location?.state?.creationId;
  const { t, i18n } = useTranslation();

  // TODO: memoize these functions so that they don't get recreaed on render
  const startResendTimer = () => {
    clearInterval(interval);
    let seconds = 5;
    setResendSeconds(seconds);
    interval = setInterval(() => {
      if (seconds > 0) {
        seconds -= 1;
      } else {
        seconds = 0;
        clearInterval(interval);
      }
      setResendSeconds(seconds);
    }, 1000);
  };

  const isDigitErrored = (digit) => {
    const number = parseInt(digit, 10);
    return !(number >= 0 && number <= 9);
  };

  const validateDigits = () => {
    setErrorMessage('');
    const digitErrors = digits.map(isDigitErrored);
    setErrors(digitErrors);
    return digitErrors.reduce((a, d) => !d && a, true);
  };

  const resetErrors = () => {
    setErrors([false, false, false, false, false]);
    setErrorMessage('');
  };

  const updateError = ({ response }) => {
    const { code, description } = response.data;
    setErrorMessage(code || description || SERVER_ERROR);
  };

  const verifyOTP = async () => {
    if (validateDigits()) {
      resetErrors();
      setIsLoading(true);

      axios.post(
        `${apiUrl}/signup/${creationId}/otp`,
        { otpPasscode: parseInt(digits.join(''), 10) },
      ).then(() => {
        navigate(`${process.env.PUBLIC_URL}/creating-account`, {
          state: { digits, creationId },
          replace: true,
        });
      }).catch((e) => {
        setErrors([true, true, true, true, true]);
        updateError(e);
      });
    } else {
      setErrorMessage('badOtpCode');
    }
    setIsLoading(false);
  };

  const resendOTP = async () => {
    // Clear the current fields since we are generating a new code
    setDigits(['', '', '', '', '']);
    resetErrors();

    // Set the timer to null, to show a 'sent message' state
    setResendSeconds(null);

    // Start the resent timer after 5 seconds
    setTimeout(startResendTimer, 10000);

    axios.put(`${apiUrl}/signup/${creationId}/otp`)
      .then(({ data }) => {
        if (typeof data === 'object') {
          const { code, otpResendAttempts } = data;
          if (otpResendAttempts) {
            setResendAttempts(otpResendAttempts);
          } else if (code) {
            throw new Error(code);
          } else {
            throw new Error(SERVER_ERROR);
          }
        } else {
          throw new Error(SERVER_ERROR);
        }
      }).catch(updateError);
  };

  const handleNumberChange = (value, index) => {
    setSelectedDigit(index);
    resetErrors();
    const newDigits = [...digits];
    newDigits[index] = value;
    setDigits(newDigits);
  };

  const goBackOneDigit = () => {
    if (selectedDigit > 0) {
      setSelectedDigit(selectedDigit - 1);
    }
  };

  const handleKeyUp = ({ key, keyCode, nativeEvent }) => {
    const hasNumLock = nativeEvent.getModifierState('NumLock');

    switch (key) {
      case 'Enter':
        verifyOTP();
        break;
      case 'Backspace':
        handleNumberChange('', selectedDigit);
        goBackOneDigit();
        break;
      default:
        // Check that NumLock is not enabled
        if (hasNumLock) {
          setErrorMessage('numLock');
        } else {
          setErrorMessage('');
        }

        // Key press is between 0 and 9 (this uses sequential keycode integers)
        if (keyCode >= 48 && keyCode <= 57) {
          handleNumberChange(key, selectedDigit);
          // If this is not the last digit index
          if (selectedDigit < digits.length - 1) {
            setSelectedDigit((d) => d + 1);
          }
        }
        break;
    }
  };

  const getTitle = () => {
    switch (errorMessage) {
      case OTP_EXCEEDED:
      case OTP_CREATION_LIMIT_EXCEEDED:
      case SERVER_ERROR:
        return t(errorMessage);
      default:
        return t('verifyEmail');
    }
  };

  const getDescription = () => {
    switch (errorMessage) {
      case OTP_EXCEEDED:
      case OTP_CREATION_LIMIT_EXCEEDED:
      case OTP_VERIFICATION_LIMIT_EXCEEDED:
      case SERVER_ERROR:
        return t(`${errorMessage}_BODY`);
      default:
        return t('enterOtp');
    }
  };

  const handlePaste = (e) => {
    // Check the clipboard
    e.clipboardData.items[0].getAsString((text) => {
      const newDigits = text.trim().split('');
      // If we have 5 characters, set the digits to the new values
      if (newDigits.length === 5) {
        resetErrors();
        setDigits(newDigits);
        setSelectedDigit(4);
      }
    });
  };

  useEffect(() => {
    setTimeout(startResendTimer, 5000);
  }, []);

  const isErrored = (errorMessage === OTP_EXCEEDED) || (errorMessage === SERVER_ERROR);

  return (
    <Box
      sx={{
        backgroundColor: 'navy',
        height: '100%',
        flex: 1,
        overflow: 'auto',
        display: 'grid',
        color: 'white',
        gridTemplateRows: 'auto auto 1fr',
        gridTemplateAreas: "'logo' 'fields' 'line'",
        '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
          display: '-ms-grid',
          msGridRows: 'auto auto 1fr',
        },
      }}
    >
      <Box mb={3} sx={{ gridArea: 'logo', msGridRow: '1' }}>
        <Link to={`${process.env.PUBLIC_URL}/`}>
          <Logo isStatic />
        </Link>
        <Box
          ml={3}
          sx={{
            position: 'absolute', top: theme.space[5], right: theme.space[3], width: [130, 200],
          }}
        >
          <CompactSelectField
            name="language"
            value={getLanguage(i18n.language)}
            handleChange={(name, value) => i18n.changeLanguage(value)}
            placeholder={t('SelectCountry')}
            options={languages}
            variant="alt"
          />
        </Box>
      </Box>
      <Flex
        sx={{
          alignItems: 'center',
          flexDirection: 'column',
          gridArea: 'fields',
          msGridRow: '1',
        }}
      >
        <Flex
          sx={{
            justifyContent: 'center',
            alignItems: 'center',
            flexDirection: 'column',
            maxWidth: isErrored ? 700 : 'auto',
          }}
        >
          <Image
            src={isErrored ? ErrorIcon : ShieldIcon}
            alt="shield"
            height={LOGO_SIZE}
            width={LOGO_SIZE}
          />
          <Text
            my={3}
            px={4}
            sx={{
              gridArea: 'fields',
              msGridRow: '1',
              fontSize: 4,
              fontWeight: 700,
              lineHeight: '32px',
              letterSpacing: '0.008em',
              WebkitFontSmoothing: 'antialiased',
              textAlign: 'center',
            }}
          >
            {getTitle()}
          </Text>
          <Text
            mb={6}
            px={4}
            sx={{
              gridArea: 'fields',
              msGridRow: '1',
              fontSize: 2,
              lineHeight: '24px',
              letterSpacing: '0.01em',
              WebkitFontSmoothing: 'antialiased',
              textAlign: 'center',
            }}
          >
            {getDescription()}
          </Text>
          {!isErrored && (
            <Box mb={5}>
              <Flex
                sx={{
                  flexDirection: ['column', 'row'],
                  alignItems: 'center',
                  mb: 3,
                }}
              >
                <form
                  autoComplete="off"
                  style={{
                    display: 'grid',
                    gridGap: 12,
                    gridTemplateColumns,
                    marginBottom: [3, 0],
                  }}
                >
                  {INPUTS.map((id, index) => (
                    <InputField
                      key={id}
                      isFullWidth
                      type="number"
                      height={INPUT_SIZE}
                      fontSize={2}
                      maxLength={1}
                      name={`input-${id}`}
                      value={digits[index]}
                      isErrored={errors[index]}
                      shouldFocusOnLoad={selectedDigit === index}
                      handleFocus={() => setSelectedDigit(index)}
                      onKeyUp={handleKeyUp}
                      onPaste={handlePaste}
                    />
                  ))}
                </form>
                <Button
                  data-testid="registerButton"
                  handleClick={verifyOTP}
                  label={t('verifyOtpLabel')}
                  isFullWidth
                  isLoading={isLoading}
                  disabled={errorMessage !== ''}
                  sx={{
                    borderRadius: 20,
                    height: 40,
                    cursor: errorMessage !== '' ? 'default' : 'pointer',
                    opacity: errorMessage !== '' ? 0.3 : 1,
                    ml: [0, 6],
                    mt: [3, 0],
                    fontSize: 1,
                  }}
                />
              </Flex>
              {errorMessage && (
                <Box sx={{
                  color: '#F29295',
                  fontSize: 0,
                  lineHeight: '20px',
                  letterSpacing: '0.01em',
                  WebkitFontSmoothing: 'antialiased',
                  alignSelf: ['none', 'flex-start'],
                  marginTop: 3,
                  mx: [5, 0],
                  opacity: errorMessage ? 1 : 0,
                }}
                >
                  {(!isLoading && t(errorMessage)) || '---'}
                </Box>
              )}
            </Box>
          )}
        </Flex>
      </Flex>
      {!isErrored && (
        <Flex px={5} sx={{ gridArea: 'line', justifyContent: 'center' }}>
          <Flex
            pb={5}
            sx={{
              borderTop: '1px solid #6C7189',
              justifyContent: ['initial', 'space-between'],
              flexDirection: ['column', 'row'],
              alignItems: ['center', 'flex-start'],
              maxWidth: 1020,
              flex: 1,
            }}
          >
            <Flex mt={6} sx={{ fontSize: 0, lineHeight: '20px', WebkitFontSmoothing: 'antialiased' }}>
              <Text mr={1} sx={{ fontWeight: 600, flexShrink: 0 }}>Tip:</Text>
              {t('checkSpamFolder')}
            </Flex>
            <ResendStatus
              resendOTP={resendOTP}
              resendSeconds={resendSeconds}
              attempts={resendAttempts}
            />
          </Flex>
        </Flex>
      )}
      {isErrored && (
        <Flex sx={{ justifyContent: 'center' }}>
          <Link to={`${process.env.PUBLIC_URL}/`} style={{ color: theme.colors.electricBlue, textDecoration: 'none' }}>
            <Button
              sx={{
                px: 5,
                fontSize: 1,
                borderRadius: 20,
                height: 40,
                cursor: 'pointer',
                '&:hover': {
                  backgroundColor: 'electricBlueHover',
                },
              }}
              label={t('signUpAgain')}
            />
          </Link>
        </Flex>
      )}
    </Box>
  );
}

OneTimePasscode.propTypes = {
  apiUrl: PropTypes.string.isRequired,
};

export default OneTimePasscode;
