import React from 'react';
import { Flex, Button, Box } from 'theme-ui';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import CompactSelectField from './CompactSelectField';
import Logo from './Logo';
import languages from '../assets/languages.json';

const getLanguage = (lang) => languages.find((l) => l.value === lang);

function Header() {
  const { t, i18n } = useTranslation();
  const navigate = useNavigate();

  return (
    <Flex
      p={[0, 0, 4, 6]}
      sx={{
        justifyContent: 'space-between',
        alignItems: ['flex-start', 'center'],
        flexWrap: 'wrap',
        flexDirection: ['column', 'row'],
      }}
    >
      <Logo smallSize />
      <Flex
        mr={3}
        sx={{
          justifyContent: 'flex-end',
          flex: 1,
          width: ['100%', 'auto'],
        }}
      >
        <Box mr={3}>
          <CompactSelectField
            name="language"
            value={getLanguage(i18n.language)}
            handleChange={(name, value) => i18n.changeLanguage(value)}
            placeholder=""
            options={languages}
          />
        </Box>
        <Box mr={3} mt={1}>
          <Button
            onClick={() => navigate('https://authentication.liveperson.net/login.html')}
            variant="secondary"
          >
            {t('SignIn')}
          </Button>
        </Box>
      </Flex>
    </Flex>
  );
}

export default Header;
