import React, { useState, useRef, useEffect } from 'react';
import { Box } from 'theme-ui';
import Select from 'react-select';
import theme from '../theme';
import DropdownIndicator from './DropdownIndicator';
import Option from './Option';
import Input from './Input';

function ValueContainer(props) {
  const value = props.selectProps?.value?.label || props.selectProps?.placeholder || '';
  const placeholderColor = props.selectProps.isAltVariant
    ? theme.colors.lightGray
    : theme.colors.darkGray;
  const textColor = props.selectProps.isAltVariant
    ? theme.colors.lightGray
    : theme.colors.darkGray;

  return (
    <Box
      data-testid="value-container"
      pl={1}
      style={{
        color: props.selectProps?.value ? textColor : placeholderColor,
        flex: 1,
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        whiteSpace: 'nowrap',
      }}
    >
      {value}
    </Box>
  );
}

/**
 * Used for selecting a single option from a defined set, but with a compact display
 *
 * @visibleName Compact Select Field
 */
function CompactSelectField({
  name,
  placeholder,
  value,
  handleChange,
  options,
  height,
  variant,
}) {
  const [menuIsOpen, setMenuIsOpen] = useState(false);
  const node = useRef();
  const isAltVariant = variant === 'alt';

  const detectOutsideClick = (e) => {
    if (!node.current.contains(e.target)) {
      setMenuIsOpen(false);
    }
  };

  // Detect outside clicks
  useEffect(() => {
    document.addEventListener('mousedown', detectOutsideClick);
    return () => {
      document.removeEventListener('mousedown', detectOutsideClick);
    };
  }, []);

  const getStyles = () => {
    const basicStyle = {
      marginLeft: 13,
      boxSizing: 'border-box',
      display: 'flex',
      alignItems: 'center',
      position: 'relative',
      borderWidth: 0,
      fontFamily: '"Roboto", sans-serif',
      fontSize: theme.fontSizes[0],
      height: theme.rowHeight,
      letterSpacing: '0.01em',
      lineHeight: '38px',
      WebkitFontSmoothing: 'antialiased',
      '::-ms-expand': {
        display: 'none',
      },
      color: isAltVariant ? 'white' : theme.colors.navyGray,
      gridArea: 'input',
    };

    return {
      indicatorSeparator: (provided) => ({
        ...provided,
        display: 'none',
      }),
      menu: (provided) => ({
        ...provided,
        backgroundColor: 'transparent',
        margin: 0,
        padding: 0,
        borderRadius: theme.space[2],
        overflow: 'hidden',
        width: 200,
        right: 0,
      }),
      menuList: (provided) => ({
        ...provided,
        margin: 0,
        padding: 0,
        borderRadius: theme.space[2],
        boxShadow: isAltVariant
          ? 'none'
          : '0px 0px 16px rgba(132, 121, 112, 0.64)',
        maxHeight: height && `${height}px`,
        overflow: 'auto',
        border: isAltVariant ? '1px solid #484B68' : 'none',
      }),
      control: () => ({
        ...basicStyle,
      }),
    };
  };

  return (
    <Box>
      <Box
        data-testid={`select-field-${name}-container`}
        sx={{ cursor: 'pointer' }}
        onClick={() => setMenuIsOpen((s) => !s)}
        ref={node}
      >
        <Select
          inputId={`select-field-${name}`}
          name={`select-field-${name}`}
          isSearchable={false}
          options={options}
          value={value}
          styles={getStyles()}
          onChange={(val) => handleChange(name, val.value)}
          placeholder={placeholder}
          components={{
            Input, ValueContainer, DropdownIndicator, Option,
          }}
          menuIsOpen={menuIsOpen}
          isAltVariant={isAltVariant}
        />
      </Box>
    </Box>
  );
}

CompactSelectField.defaultProps = {
  name: 'select-field',
  value: undefined,
  options: [],
  height: 300,
};

export default CompactSelectField;
